
* {
  margin: 0;
  padding: 0;
  border: 0;
  min-height: 0;
  min-width: 0;
}
*:focus {
  outline: none;
}
body {
  overflow: hidden;
  background: #000;
}

.home ul,
.home .year {
  color: #fff;
  list-style-type: none;
  padding: 0;
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: 60rem;
  width: 60rem;
  font-size: 16rem;
  -webkit-transition: 0.1s 0.1s ease-in;
  transition: 0.1s 0.1s ease-in;
}
.home ul li,
  .home .year li {
    position: absolute;
    height: 60rem;
    width: 60rem;
    color: #fff;
    text-align: center;
    line-height: 60rem;
    font-size: 14rem;
}
.home ul li.hover,
    .home .year li.hover {
      text-shadow: #ff6666 0rem 0rem 10rem, #ff9900 0rem 0rem 20rem, #ff99cc 0rem 0rem 30rem, #66cccc 0rem 0rem 40rem, #ff99cc 0rem 0rem 70rem, #ccff66 0rem 0rem 80rem, #ff99cc 0rem 0rem 100rem;
}
.year {
  z-index: 99;
  text-align: center;
  line-height: 60rem;
}

